// Libraries
import { Switch, Route, useLocation } from "react-router-dom";
import { TransitionGroup, CSSTransition } from "react-transition-group";

// Components
import Home from "./components/Home/Home";
import Header from "./components/Layouts/Header/Header";
import Footer from "./components/Layouts/Footer/Footer";
import ScrollToTop from "./components/utils/ScrollToTop";
import Contact from "./components/Layouts/Contact/Contact.jsx";
import WhatsAppButton from "./components/Layouts/WhatsAppButton/WhatsAppButton.jsx";
import Desarrollos from "./components/Desarrollos/Desarrollos.jsx";
import Prensa from "./components/Prensa/Prensa";
import Constructora from "./components/Constructora/Constructora";
import Ubicacion from "./components/Home/Ubicacion/Ubicacion";
import Profesionales from "./components/Profesionales/Profesionales";
import Noticia from "./components/Prensa/Noticia/Noticia";

// Styles
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.scss";
import { NotFound } from "./components/NotFound/NotFound";

const App = () => {
  const location = useLocation();

  return (
    <>
      <Header />
      <TransitionGroup>
        <CSSTransition key={location.key} classNames="fade" timeout={300}>
          <Switch location={location}>
            <Route exact path="/">
              <ScrollToTop />
              <Home />
            </Route>

            <Route path="/desarrollos/:category">
              <ScrollToTop />
              <Desarrollos />
            </Route>

            <Route exact path="/constructora">
              <ScrollToTop />
              <Constructora />
              <Contact />
              <Ubicacion />
            </Route>

            <Route path="/constructora/profesionales">
              <ScrollToTop />
              <Constructora />
              <Profesionales />
              <Contact />
              <Ubicacion />
            </Route>

            <Route exact path="/prensa">
              <ScrollToTop />
              <Prensa />
            </Route>

            <Route exact path="/prensa/:idNoticia">
              <ScrollToTop />
              <Noticia />
            </Route>

            <Route component={NotFound} />
          </Switch>
        </CSSTransition>
      </TransitionGroup>
      <Footer />
      <WhatsAppButton />
    </>

    // <>
    //   <Header />
    //   <Switch>
    //     {/* HOME */}
    //     <Route exact path="/">
    //       <ScrollToTop />
    //       <Home />
    //     </Route>

    //     {/* DESARROLLOS */}
    //     <Route path="/desarrollos/:category">
    //       <ScrollToTop />
    //       <Desarrollos />
    //       <Despliegue />
    //     </Route>

    //     {/* CONSTRUCTORA */}
    //     <Route exact path="/constructora">
    //       <ScrollToTop />
    //       <Constructora />
    //       <Contact />
    //       <Ubicacion />
    //     </Route>

    //     {/* CONSTRUCTORA/PROFESIONALES */}
    //     <Route path="/constructora/profesionales">
    //       <Constructora />
    //       <Profesionales />
    //       <Contact />
    //       <Ubicacion />
    //     </Route>

    //     {/* PRENSA */}
    //     <Route exact path="/prensa">
    //       <ScrollToTop />
    //       <Prensa />
    //     </Route>

    //     {/* PRENSA/NOTICIA */}
    //     <Route exact path="/prensa/:idNoticia">
    //       <Noticia />
    //     </Route>

    //     {/* NOT FOUND*/}
    //     <Route component={NotFound}></Route>
    //   </Switch>
    //   <Footer />
    //   <WhatsAppButton />
    // </>
  );
};

export default App;
